body {
  background: url(https://www.top-sevastopol.ru/images/banery/3d/Switch-2-Web123.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.headline {
  color: white;
  margin: 0 auto;
  font-size: 50px;
  display: table;
}

.link-conteiner {
  
  height: max-content;
  display: flex;
  flex-direction: column;
  width: max-content;
  margin: 250px auto;
  
}

.link {
  width: 400px;
  height: 40px;
  
  background-color: rgb(44, 44, 199);
  margin-bottom: 10px;
  border-radius: 20px;
  color: white;
  text-align: center;
  font-size: 30px;
  text-decoration: none;
}

.link:hover {
  background-color: orange;
  transition: 3s;
  color: black;
}

.annonce-list-conteiner {
  margin-left: 0; 
  padding-left: 0;
}

.annonce-list {
  list-style-type: none;
  width: 40%;
  margin: 0 auto;
  background-color:rgb(238, 119, 119);
  margin-bottom: 15px;
  padding-bottom: 20px;
}

.title {
  font-size: 30px;
  margin: 0 auto;
  width: max-content;
}

.annonce-list-item {
  list-style-type: none;
  width: 40%;
  margin: 0 auto;
  background-color: orange;
  color: white;
  margin-bottom: 15px;
  transition: 2s;
}
 
.annonce-list-item:hover {
  width: 45%;
  background-color:rgb(240, 53, 53);
  color: black;
  cursor: pointer;
}

.annonce-title {
  background-color: rgb(240, 53, 53);
  text-align: center;
  font-size: 30px;
}

.annonce-description {
  height: max-content;
  font-size: 20px;
  padding-top: 5px;
  padding-left: 10px;
  padding-right: 10px;
  color: white;
  white-space: pre-wrap;
  overflow: hidden;
  margin-bottom: 10px;
  
}

.annonce-footer {
  display: flex;
  justify-content: space-between;
  align-items: space-between;
  margin: 0px 10px 0px 10px;
  padding-bottom: 20px;
  padding-top: 20px;
}


.btn {
  width: 70px;
  height: 30px;
  text-align: center;
  outline: 0;
  border: 0;
  background-color:  rgb(240, 53, 53);
  border-radius: 15px;
  transition: 2s;
  
}

.btn:hover {
  background-color: orange;
  cursor: pointer;
}

.btn-edit-link {
  width: 70px;
  height: 30px;
  text-align: center;
  outline: 0;
  border: 0;
  background-color:  rgb(240, 53, 53);
  border-radius: 15px;
  transition: 2s;
  color: black;
  text-decoration: none;
  line-height: 30px;
}

.btn-edit-link:active {
  color: black;
  text-decoration: none;
}

.btn-edit-link:hover {
  background-color: orange;
  cursor: pointer;
  color: black;
}

.annonce-date {
  font-size: 20px;
  color: white;
}

.simular-anounce-conteiner {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10px;
  margin-right: 10px;
  flex-direction: column;
}

.simular-announce-header {
  text-align: center;
  background-color:orange;
  margin: 0 10px 0 10px;
  font-size: 30px;
  color: white;
}

.simular-announce-header:hover {
  cursor: pointer;
}

.simular-anounce {
  background-color: orange;
  height: max-content;
  width: 80%;
  font-size: 30px;
  text-align: center;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
  margin-bottom:10px ;
}

.simular-anounce:hover {
  cursor: pointer;
}

.nav-conteiner {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.serchInput {
  margin-right: 30px;
  height: 25px;
  width: 350px;
  border-radius: 15px;
  outline: none;
  padding-left: 15px;
  border: none;
}

.home-link {
  width: 100px;
  border: 1px blue solid;
  text-align: center;
  color: white;
  height: 25px;
  border-radius: 13px;
  text-decoration: none;
  padding-top: 5px;
  transition: 2s;
}

.home-link:hover {
  background-color: blue;
}

.form-container {
  width: max-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form-title-input {
  width: 300px;
  height: 25px;
  border-radius: 10px;
  outline: none;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  margin-bottom: 20px;
  margin-top: 20px
}

.form-description-input {
  width: 300px;
  border-radius: 10px;
  outline: none;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  height: 200px;
  text-align: start;
  resize: none;
  padding: 10px 10px 0 10px;
}

.form-btn-container {
  display: flex;
  justify-content: space-around;
  align-items: space-between;
  width: 300px;
  margin-top: 20px
}

.form-btn {
  width: 100px;
  height: max-content;
  font-size: 25px;
  background-color: blue;
  text-align: center;
  text-decoration: none;
  color: white;
  border-radius: 15px;
  transition: 1s;
}

.form-btn:hover {
  background-color: orange;
  color: black;
}

placeholder {
  text-align: center;
}
